import {configList} from "@/api/basic";
import {merchantView} from "@/api/merchant";

import rfImage from "@/components/rf-image/rf-image";
export default {
		components: {
			rfImage,
		},
	data() {
		return {
			detail: {},
			title: null,
			loading: true
		}
	},
	onLoad(options) {
		this.initData(options);
	},
	methods: {
		/**
		 *@des 初始化数据
		 *@author stav stavyan@qq.com
		 *@blog https://stavtop.club
		 *@date 2019/12/19 15:16:17
		 */
		initData (options) {
			this.title = options.title;
			uni.setNavigationBarTitle({
				title: options.title
			});
			this.getConfigList(options.field);
		},
		async getConfigList(field) {
	    if (field.indexOf('protocol') !== -1) {
				await this.$get(`${configList}`, {
					field,
				}).then(r => {
			    this.loading = false;
					this.detail = r.data
				}).catch(err => {
			    this.loading = false;
					console.log(err)
				});
	    } else {
			  const userInfo = uni.getStorageSync('userInfo');
		    if (!userInfo) return;

				await this.$get(`${merchantView}`, {
					id: userInfo.merchant_id,
					field,
				}).then(r => {
			    this.loading = false;
					this.detail = r.data
				}).catch(err => {
			    this.loading = false;
					console.log(err)
				});
	    }
		}
	}
}
